<template>
	<div id="app">
		<Layout>
			

			<loading :active.sync="isLoading" :can-cancel="false" :is-full-page="true" color="#4A89DC"/>
			
			<Header>
				<div v-if="menu ===true">
					<Slide  width="150">
						<a id="home" href="#"> 
							<span @click="dashboard">Dashboard</span>  
						</a>
						<a id="home" href="#"> 
							<span @click="administrador" >Administracion</span>  
						</a>
						<a id="pedidos" href="#"> 
							<span @click="pedidos" >Pedidos</span>  
						</a>
						<a id="home" href="#"> 
							<span>Cambiar rol
							</span>  
						</a>
						<a id="home" href="#"> 
							<span @click="cerrar" >Cerrar Sesion</span>  
						</a>
					</Slide>
				</div> 
				<AppHeader> </AppHeader>
			</Header>
			
			

			

			<Content :style="{padding: '0 30px'}">
				<Notifications/>
				<router-view/>
			</Content>
		</Layout>
	</div>
</template>


<script>
	import Loading from 'vue-loading-overlay';
	import 'vue-loading-overlay/dist/vue-loading.css';


	import AppHeader from "./components/Header"
	import Notifications from "./components/Notifications/Popup"

	import {Slide}  from 'vue-burger-menu'
	
    export default {
		data(){
			return {
				isLoading: false,
				menu:true
			}
		},
		methods: {
            dashboard(){
				this.menu=true;
                this.$router.push('/dashboard')
            },

			 pedidos(){
				this.menu=true;
                this.$router.push('/pedidos')
            },

			update(item){
                EventBus.$emit("update-info-popup", item)
            },

			cerrar(){
                this.$router.push('/')
            },

			administrador(){
                this.$router.push('/Administracion')
            }
        },
		mounted(){
			EventBus.$on("toggle-loading", (value) => {
				if(value){
					this.$Spin.show({
						render: (h) => {
							return h('div', [,
								h('img', {
									'class': 'loading-logo',
									attrs: {
										'src': "/img/logo-wics.jpeg",
									},
								}),
								h('Icon', {
									'class': 'demo-spin-icon-load',
									props: {
										type: 'ios-loading',
										size: 35
									}
								}),
							])
						}
					});
				}
				else {
					this.$Spin.hide();
				}
			})

			
		},
        components: {
			Loading,
			AppHeader,
			Notifications,
			Slide ,
        } 
    }

	

</script>

